@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,400i);
@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400);
:root {
  --lt-pink: #FFEBFF;
  --med-pink: #D6B4D6;
  --bg-navy: #1B182E;
  --bg-dark: #08080c;
  background: #08080c;
  background: var(--bg-dark);
}

* {
  box-sizing: border-box;
}

html, body {
  background: #08080c;
  background: var(--bg-dark);
  margin: 0 auto;
  height: 100%;
  font-family: "Roboto Mono", monospace;
  color: #D6B4D6;
  color: var(--med-pink);
}

body {
  position: relative;
  height: 100%;
  background: #08080c;
  background: var(--bg-dark);
}

p {
  color: #D6B4D6;
  color: var(--med-pink);
  font-size: 0.8em;
}

#app {
  padding: 6em;
  background: #08080c;
  background: var(--bg-dark);
}

@media screen and (max-width: 600px) {
  #app {
    padding: 1em;
  }
}

#canvas {
  position: absolute;
}
#info {
  max-width: 460px;
  line-height: 22px;
  position: absolute;
  bottom: 0;
}

.borderline {
  position: relative;
  height: 2px;
  margin-bottom: 24px;
  background: var(--med-pink);
}

span#info-label {
  color: var(--lt-pink);
  letter-spacing: 1.2px;
  font-size: 22px;
}

div#info-content {
  margin-top: 24px;
  margin-bottom: 28px;
}

div#info-content p {
  font-size: 0.8em;
}

img#ig-logo {
  height: 22px;
  width: 22px;
  margin-right: 16px;
}

img#email-logo {
  height: 22px;
}
.inner-page-container {
  overflow-x: auto;
}

.page-header {
  position: relative;
}

h1.page-header-label {
  font-size: 20px;
  text-transform: uppercase;
  color: var(--lt-pink);
  display: inline-block;
  font-weight: 100;
}

.project-header-label-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

div.page-header-divider {
  display: inline-block;
	height: 2px;
	background: var(--lt-pink);
  width: 48px;
  left: 128px;
	position: absolute;
	top: 50%;
}

div.page-content {
  line-height: 26px;
  max-width: 580px;
}

div.page-content.about p {
  font-size: 1.0em;
  line-height: 1.7em;
  font-weight: 200;
}

@media screen and (max-width: 600px) {
  div.page-content {
    margin-left: 0px;
    margin-right: 0px;
  }
}
.projects-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.project-detail-container {
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.project-content p {
  font-size: 0.775em;
  line-height: 1.6em;
  color: var(--med-pink);
}

.project-content {
  margin-bottom: 3.5em;
}

.project-container {
  overflow: hidden;
  margin-bottom: 64px;
}

.project-content-imgs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.LazyLoad.proj-img-container.full {
  width: 100%;
}

.LazyLoad.proj-img-container.half {
  width: 50%;
  padding: 16px;
}

.project-img {
  width: 72%;
  float: left;
}

img.project-highlight {
  width: 100%;
  height: auto;
}

.project-meta {
  float: right;
  width: 25%;
  overflow:hidden;
}

.project-description {
  border: 2px solid var(--med-pink);
  padding: 12px;
}

.proj-img {
  width: 100%;
}

.project-description {
  margin-top: -2px;
}

.project-description p {
  color: var(--lt-pink);
  margin: 0px;
  line-height: 22px;
}

/* GRID VIEW */
.content-grid-item-outer {
  width: 30%;
  margin-bottom: 8em;
  padding: 0px;  
  min-width: 400px;
}

.content-grid-item-outer.placeholder {
  margin-bottom: 0px;
}

.project-img-grid {
  width: 100%;
}

.project-meta-container {
  margin-top: 1.2em;
  position: relative;
}

.project-name {
  text-transform: uppercase;
  color: var(--lt-pink);
  letter-spacing: 0.02em;
}

a.project-link {
  color: var(--lt-pink)!important;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  position: absolute;
}

a.project-link:after {
  content: "";
  bottom: -3px;
  height: 1px;
  width: 100%;
  background: var(--med-pink);
}

.project-desc-container {
  padding-top: 2.2em;
}

.project-desc-container p {
  margin: 0px;
  line-height: 1.6em;
  font-size: 0.775em;
  color: var(--med-pink);
}

.project-detail-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 28px;
}

.project-detail-link {
  display: inline-block;
  padding: 8px 16px;
  font-size: 1em;
  margin-top: 36px;
  margin-bottom: 36px;
  border: 1px solid var(--med-pink);
  border-radius: 3px;
  background: var(--med-pink);
  color: var(--bg-navy);
  font-size: 0.875em;
}

div.vid {
  position: relative;
  overflow: hidden;
  padding-top: 62.25%;
  margin-bottom: 60px;
}

div.vid.cepheids {
  background: black;
}

div.vid.propagator {
  padding-top: 75%;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.player {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}

.project-content-meta {
  display: flex;
  justify-content: space-between;
  margin-bottom: 42px;
}

.project-content-desc {
  /* padding-top: 8px; */
  color: var(--lt-pink);
  width: 70%;
  margin-top: -8px;
  /* border-top: 1px solid var(--med-pink); */
}

.project-content-desc p {
  font-size: 0.9em;
}

.project-content-tools {
  color: var(--lt-pink);
  width: 20%;
  border-top: 0.5px solid var(--med-pink);
  padding-top: 8px;
}

.project-content-tools ul {
  margin: 12px 0px;
  padding: 0px;
}

.project-content-tools ul li {
  list-style-type: none;
  font-size: 0.8em;
  margin-top: 4px;
  margin-bottom: 4px;
  color: var(--med-pink);
}

@media screen and (max-width: 980px) {
  .content-grid-item-outer {
    width: 85%;
    min-width: 100%;
    margin-bottom: 5em;
  }
  .placeholder {
    margin-bottom: 0;
    display: none;
  }
  .project-detail-container {
    max-width: 95%;
    margin-top: 0px;
  }
  .back-icon {
    display: none;
  }
}

@media screen and (max-width: 1400px) {
  .content-grid-item-outer {
    width: 47%;
  }
}

@media screen and (max-width: 768px) {
  div.vid.propagator {
    padding-top: 195%;
  }
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 4em;
  font-size: 1.2em;
  letter-spacing: 1px;
  font-weight: 100;
}

span#name-label {
  letter-spacing: 0.5px!important;
  font-weight: 200;
}

span#name-label:hover {
  cursor: default;
}

.nav-items ul {     
  margin: 0;
  padding: 0;
}

.nav-items li {
  list-style-type: none;
  display: inline-block;
  position: relative;
  margin-right: 1em;
  margin-left: 1em;
}
 
a, a:visited { 
  text-decoration: none;
  color: var(--med-pink);
  text-transform: uppercase;
  font-size: 0.9em;
}

.nav-link.active:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 1px;
  background: var(--med-pink);
  width: 100%;
}

@media screen and (max-width: 600px) {
  .nav-items {
    width: 100%;
  }

  .nav-items ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-around;
  }

  .nav-items ul li {
    margin-left: 0;
  }

  span#name-label {
    display: none;
  }
}
